@import "../_cms.less";
@import "./_baby-variables.less";
@import "./_baby-mixins.less";

.iconfont-face(@baby-icon-name, @baby-icon-url);

.@{baby-icon-name}{
	.iconfont-mixin(@baby-icon-name);
}

.@{container-class}{

	.mod-title{
		overflow: hidden;
		position: relative;
		border-left:4px @baby-color solid;
		background: #f9f9f9;
		padding:10px 10px 10px 6px;
		color: @baby-color;
		font-weight: normal;

		.title{
			float: left;
		}

		.more{
			float: right;
			color: #999;
		}
	}

}
