@import "../_baby-variables.less";
@import "../_baby-mixins.less";

.bg-fff{

	background:#fff;

	.header{
		background:#f9f9f9;
	}
}

.fn-@{cms-ns}-sort{
	margin:15px 0;
	overflow:hidden;

	h3{
		padding-left: 20px;
		a{
			display:block;
			font-size:@font-size-xl;
		}
	}

	.sort-sp-list{
		overflow:hidden;
		padding:0 20px;
		.list{
			width:25%;
			height:100%;

			float:left;
			position:relative;

			&:before{

				content:"";
				display:block;
				padding-top:100%;
				padding-bottom:20px;
			}
			.list-item{

				position:absolute;
				top:0;
				left:0;
				width:90%;
				height:100%;
				margin:0 7%;

			}

			span, span .img{
				width:100%;
				height:100%;
				display:block;
			}
			p{
				width: 100%;
				text-align:center;
				padding: 5px 0;
				.ellipsis();
			}
		}
	}
}
